<template>
    <d2-container>
        <!-- 配送管理 -->
        <!-- header -->
        <template slot="header">
            <el-row>
                <el-col :span="3" style="margin-right:10px;">
                    <!-- <el-autocomplete class="inline-input" v-model="state1" :fetch-suggestions="querySearch" placeholder="请输入内容"></el-autocomplete> -->
                    <el-alert title="配送管理" type="info" :closable="false">
                    </el-alert>
                </el-col>
                <el-col :span="5">
                    <!-- <el-button type="primary" plain>查询</el-button> -->
                    <el-button type="primary" plain @click="add_val">添加</el-button>
                </el-col>
            </el-row>
        </template>
        <el-table :data="tableData" border style="width: 100%">

            <el-table-column type="index" width="50">
            </el-table-column>
            <el-table-column prop="distribution_title" label="公司名称" width="180">
            </el-table-column>
            <el-table-column prop="distribution_desc" label="公司描述" width="180">
            </el-table-column>
            <el-table-column prop="distribution_fee" label="配送费用--单位￥">
            </el-table-column>
            <el-table-column prop="created_at" label="创建时间">
            </el-table-column>
            <el-table-column prop="updated_at" label="更新时间">
            </el-table-column>
            <el-table-column label="操作" width="250">
                <template scope="val">
                    <el-button type="primary" plain @click="val_edit(val.row)">编辑</el-button>
                    <!-- <el-button type="primary" plain>修改</el-button> -->
                    <el-button type="danger" plain @click="val_clear(val.row)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <!-- footer -->
        <el-row type="flex" justify="space-between" align="middle" style="height: 80px;">
            <el-col :span="24">
                <!-- <el-checkbox v-model="checked" style="margin: 0 10px;"></el-checkbox>
                <el-button type="danger">批量删除</el-button> -->
                <el-pagination layout="prev, pager, next" @current-change="page" :page-size="10" :total="checked" style="float: right;">
                </el-pagination>
            </el-col>
        </el-row>



        <!-- 添加公司 -->
        <el-dialog :title="num" :visible.sync="dialogVisible" width="30%" :before-close="clear">
            <el-input placeholder="请输入公司名称..." v-model="form.distribution_title" style="margin-top: 10px;">
                <template slot="prepend">公司名称</template>
            </el-input>
            <el-input placeholder="请输入公司描述..." v-model="form.distribution_desc" style="margin-top: 10px;">
                <template slot="prepend">公司描述</template>
            </el-input>
            <el-input placeholder="请输入配送费..." v-model="form.distribution_fee" style="margin-top: 10px;">
                <template slot="prepend">配送费</template>
            </el-input>
            <span slot="footer" class="dialog-footer">
                <el-button @click="clear">取 消</el-button>
                <el-button type="primary" @click="onsubmit" v-if="!form.id">确 定</el-button>
                <el-button type="primary" @click="onsubmit_edit" v-if="form.id">确 定</el-button>
            </span>
        </el-dialog>
    </d2-container>
</template>

<script>
    import { add_distribution, show_distribution, edit_distribution, del_distribution } from '../../plugin/axios/api.js';
    import { Myerror } from '../../plugin/axios/er.js';
    import qs from 'qs';
    export default {
        name: 'Distribution_home',
        data() {
            return {
                num: '',
                value3: '',
                state1: '',
                checked: 0,
                multipleSelection: [],
                tableData: [],
                pages: 1,
                dialogVisible: false,
                form: {
                    distribution_title: '',
                    distribution_desc: '',
                    distribution_fee: '',
                }
            }
        },
        methods: {
            querySearch(queryString, cb) {
                var restaurants = this.restaurants;
                var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;
                // 调用 callback 返回建议列表的数据
                cb(results);
            },
            createFilter(queryString) {
                return (restaurant) => {
                    return (restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
                };
            },
            page(a) {//分页
                console.log(a);
                this.pages = a;
                this.initsupplier()
            },
            init_distribution() {//初始化
                show_distribution(this.pages).then(res => {
                    console.log(res);
                    this.tableData = res.data.data;
                    this.checked = res.data.paginate.total;
                })
            },
            add_val() {//添加
                this.num = "添加公司";
                this.dialogVisible = true;
            },
            onsubmit() {//添加
                let arr = qs.stringify(this.form);
                add_distribution(arr).then(res => {
                    this.msg(res)
                })
            },
            val_edit(a) {//编辑
                this.num = "编辑公司"
                this.form = {
                    distribution_title: a.distribution_title,
                    distribution_desc: a.distribution_desc,
                    distribution_fee: a.distribution_fee,
                    id: a.id
                }
                this.dialogVisible = true;
            },
            onsubmit_edit() {//编辑
                let par = {
                    distribution_title: this.form.distribution_title,
                    distribution_desc: this.form.distribution_desc,
                    distribution_fee: this.form.distribution_fee,
                }
                let id = this.form.id;
                edit_distribution(par, id).then(res => {
                    this.msg(res)
                })
            },
            val_clear(a){//删除
                del_distribution(a.id).then(res=>{
                    this.msg(res)
                })

            },
            clear() {//格式化
                this.dialogVisible = false;
                this.form = {
                    distribution_title: '',
                    distribution_desc: '',
                    distribution_fee: '',
                }
            },
            msg(res) {//返回参数处理
                if (res.code) {
                    this.$message({
                        message: res.message,
                        center: true,
                        type: 'success'
                    });
                    this.clear()
                    this.init_distribution()
                    this.dialogVisible = false;
                    return
                }
                Myerror(res, this)
            }
        },
        mounted() {
            this.init_distribution()
        }
    }
</script>
<style lang="scss">
</style>



import { add_distribution, show_distribution, edit_distribution, del_distribution } from '../../plugin/axios/api.js';